  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed Regular"), local("RobotoCondensed-Regular"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Mono Regular"), local("PTMono-Regular"), url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

/* stylelint-disable-next-line value-keyword-case */
*,
*::before,
*::after {
  box-sizing: border-box; }

.html {
  position: relative;
  font-size: 100%; }

a {
  text-decoration: none;
  color: inherit; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.section-block {
  position: relative;
  overflow: hidden;
  padding-top: 1px;
  padding-right: 40px;
  padding-bottom: 1px;
  padding-left: 40px; }
  @media (max-width: 1023px) {
    .section-block {
      padding-top: 1px;
      padding-right: 24px;
      padding-bottom: 1px;
      padding-left: 24px; } }
  @media (max-width: 767px) {
    .section-block {
      padding-top: 1px;
      padding-right: 14px;
      padding-bottom: 1px;
      padding-left: 14px; } }

.container {
  position: relative;
  max-width: 1415px;
  margin-right: auto;
  margin-left: auto; }

.button {
  margin: 0;
  padding: 0;
  cursor: pointer;
  text-decoration: none;
  border: none;
  background-color: transparent;
  user-select: none;
  font-family: inherit;
  color: inherit;
  letter-spacing: inherit;
  font-size: inherit;
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed", -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  position: relative;
  display: inline-block;
  overflow: hidden;
  transition: all 0.2s ease;
  min-height: 45px;
  min-width: 100px;
  padding: 14px 25px;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0.05em;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: transparent; }
  @media (min-width: 1024px) {
    .button:hover {
      opacity: 0.7; } }
  .button:active {
    opacity: 0.5; }

.intro {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono", -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  min-height: 878px;
