      padding-top: 92px;
      padding-bottom: 20px;
      background-image: url("../img/intro-tablet.jpg"); } }
  @media (max-width: 767px) {
    .intro {
      min-height: 647px;
      padding-top: 72px;
      padding-bottom: 10px;
      background-position: top left;
      background-image: url("../img/intro-mobile.jpg"); } }

.intro__wrapper {
  width: 750px;
