      width: 290px;
      margin-top: 215px;
      margin-left: 0; } }

.intro__title {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed", -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 30px 0 0;
