  @media (max-width: 767px) {
    .intro__title {
      font-size: 40px; } }

.intro__text {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono", -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  position: relative;
  margin: 25px 0;
  padding-left: 90px;
  font-size: 22px;
  line-height: 1.57; }
  .intro__text::before {
